<template>
  <div class="yoda">
    <h1>Welcome to YodaBot</h1>
    <input type="button" id='history' v-on:click='show = !show' value="History"/>
    <div v-if="show" id="history_chat">
        <div class="chat" v-if="history_chat.length!=0">
            <ul>
                <ListMessages
                    v-for="msg in history_chat"
                    :key="msg.id"
                    :msg="msg"
                />
            </ul>
        </div>
        <div v-else>
            <label>No history chat</label>
        </div>
    </div>                 
    <div class="container-fluid">
            <div class="row">
                <div class="col-md-6 offset-md-3">
                    <div class="container-chat">
                        <div class="divForm">
                            <div>
                                <ul v-if="messages.length>0">
                                    <ListMessages
                                        v-for="msg in messages"
                                        :key="msg.id"
                                        :msg="msg"
                                    />
                                </ul>
                                <input
                                    type="text"
                                    class="input"
                                    v-model="query"
                                    v-on:keyup.enter="search()"
                                >
                                <button v-on:click="search()">Send</button>
                                <div >
                                    {{writting}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>

import axios from 'axios'
import ListMessages from './ListMessages.vue'

const user = "You"
const yoda = "Yoda"
export default {
	components: {
		ListMessages
	},
  data () {
    return {
            query: '',
            writting:'',
            apiKey:'',
            countNotFound:0,
            show:false,
      messages: [
				{
					who: yoda,
                    text: 'Ask me anything'
				}
            ],
      history_chat: []
    }
  },
	methods: {
		search () {
            this.writting = 'writting...'
            const trimmedText = this.query.trim()
            let metodo='search';
			if (trimmedText!='') {
                this.addMessage(user,trimmedText)
                if(trimmedText.includes("force")){
                    metodo="force"
                }else if(trimmedText.includes("characters")){
                    metodo="characters"
                }
                axios
                .get('functions/ajaxGetPeticion.php',{
                //.get('resposta.txt',{
                    params: {
                        query: this.query,
                        method:metodo,
                        auth:this.apiKey
                    }
                })
                .then(response => response.data.answers.forEach(answer => {
                        if(answer.message.includes("I'm sorry")||(answer.message.includes("nothing I can find"))){
                            this.countNotFound++;
                        }else{
                            this.countNotFound=0;
                        }
                        this.addMessage(yoda,answer.message)
                    })
                )          
            }
            if(this.countNotFound>=2){
                this.query = "characters"
                this.search()
            }  
            this.writting = ''
            this.query =''
        },
        addMessage (user,data) {
            this.messages.push({
                who: user,
                text: data
            })
        },
        getAuth(){
            axios
                .post('functions/ajaxGetAuth.php',{
                    params: {
                        auth:this.apiKey
                    }
                })
                .then(response => (this.apiKey=response.data))
        },
        getHistory(){
            axios
                .post('functions/ajaxGetHistory.php',{
                    //.get('resposta.txt',{
                    params: {
                        auth:this.apiKey
                    }
                })
                .then((response) =>{ 
                        if(response.data!=''){
                            response.data.forEach(answer => {
                                this.history_chat.push({
                                    who: answer.user,
                                    text: answer.message
                                })
                            })
                        }
                        
                }).then(this.getAuth)
        }
    },
    mounted :function () {
        this.getHistory()
    }
}

</script>

<style scoped>
li{
   list-style-type: none; 
}
</style>